<template>
  <div class="product-thumb">
    <h1>{{ prod.name }}</h1>
    <img :src="prod.link" :alt="prod.name">
  </div>
</template>

<script>
export default {
  name: 'product-thumb',
  props: {
    prod: Object
  }
}
</script>



